}

.slogan-span {
    color: #FFC904;
}

.nav-item {
    font-size: 1.5rem;
}

.brand {
    color: #fff;
}




.about {
    text-align: center;
    background-color: #FFC904;
    height: 100%;
    width: 100;
};

.section {
    margin: none;
    overflow: hidden;
}

.shell {
    width:80%;
    height: 100%;
    align-self: center;
    margin:auto;
}

.about-header {
    font-size: 5rem;
    text-align: center;
}

.about-description {
    color: black;
    font-size: 1.8rem;
    font-weight: bold
}

.list-span {
    font-weight: bold;
    font-size: 2rem;
}

.brand {
    color: #fff !important;
}


.promo {
    text-align: center;
    background-color: black;
    height: 100%;
    width: 100;
};

.section {
    margin: none;
    overflow: hidden;
}

.shell {
    position: relative;
    top: 5vh;
    width:80%;
    height: 80%;
    align-self: center;
    margin: auto;
}

.promo-header {
    font-size: 5rem;
    color: #FFC904;
    text-align: left;
}

.shell-body {
    color: #fff;
}
.searchForm {
    width: 50%;
    padding: 0px 0 20px 0;
    margin: auto;
}

.search-label {
    font-weight: bold;
    font-size: 2rem;
}

#search-jumbo {
    background-color: #FFC904;
    text-align: center;
    margin-bottom: 0;
}

input {
    font-weight: bold;
}

.search-none {
    text-align: center;
    width: 100%;
    height: 600px;
    margin-top: 0;
    background-color: #fff;
}

.no-results {
    font-size: 4rem;
    position: absolute;
    z-index: 2;
    top: 35vh;
    left: 53vh;
    color: white;
}

.search-title {
    width: 100%;
    height: 50px;
    background-color: black;
    border-bottom: 2px solid #FFC904;
    text-align: center;
    margin: none;
    padding: 0;
}


.search-title-text {
    color: #fff;
    padding: 5px;
}

.navbar {
    background-color: black;
}

.nav-react-link {
    text-decoration: none;
    color: rgb(207, 207, 207);
}

.nav-react-link:hover {
    text-decoration: none;
    color: #fff;
}

.nav-item {
    display: inline-block;
}

.no-results-img {
    height: 300px;
    width: 700px;
    z-index: 1;
    position: relative;
    top: 20vh;
}

.no-results-container {
    position: relative;
}

.search-results {
    height: 400px;
    width: 100%;
    overflow-y: scroll;
    padding: 20px; 
}


.card {
    border: 2px solid black;
    padding: 10px;
    margin: 5px;
    height: 300px;
    width: 325px;
    float: left;
    text-align: center;
}

.card-body{
    width: 100%;
    height: 100%;
}


.list-group-item {
    font-weight: bold;
}


#class-jumbo {
    background-color: #FFC904;
    margin-bottom: 0;
}

.nav-pills {
    background-color: black;
    border-bottom: 2px solid #FFC904;
}

.class-review-btn {
    cursor: pointer;
}

.class-form-btn {
    cursor: pointer;
}


.no-reviews-container {
    text-align: center;
    width: 100%;
    height: 700px;
    overflow-y: scroll;
}

.no-reviews {
    position: relative;
    top: 20vh;
}

.display-4 {
    font-weight: bold;
}

.class-review-form {
    padding: 10px;
}

.class-form-label {
    font-size: 1.5rem;
}

.student-review-card {
    border: 2px solid black;
    width: 80%;
    margin-left: 10%;
    display: block;
    padding: 0;
}

.student-info {
    display: block;
    margin-left: 15%;
    margin-top: 3%;
    padding: 10px;
}


.review-info {
    float: left;
    text-align: left;
    width: 32%;
    height: 100%;
    border-right: 2px solid black;
}

.summary-section {
    float: right;
    text-align: center;
    width: 68%;
    height: 100%;
    overflow-y: scroll;
}

.summary-title {
    text-decoration: underline;
}

.summary {
    text-align: left;
    margin-left: 5%;
    padding: 10px;
    font-weight: 120%;
    font-size: 1.25rem;
}

.signUp {
    width: 500px;
    height: 50vh;
    border: 2px solid #FFC904;
    background-color: #fff;
    border-radius: 5px;
    margin: auto;
    margin-top: 25vh;
    padding: 10px;
    z-index: 1;
}

.signUp-head {
    text-align: center;
}

.signUp-title {
    color: #fff;
    text-align: center;
    position: relative;
    top: 20vh;
    font-size: 3rem;
}


.signUp-link {
    text-align: center;
    text-decoration: none;
    color: #FFC904;
}

.signUp-link:hover {
    font-size: 110%;
}

.form-group-signUp {
    padding:0 10px 0 10px;
}

.signUp-label {
    padding-left: 10px;
    padding-top: 5px;
}

.signUp-btn{
    text-align: center;
    width: 75px;
    height: 35px;
    border-radius: 5px;
    background-color: #FFC904;
    color: #fff;
    padding: 5px;
    margin: 2%;
}

.signUp-btn:hover {
    background-color: #FFC904;
    color: black;
    cursor: pointer;
}
